/* Define standard variables and values for website */
/* Use the variables */
body {
  font-family: "Roboto", -apple-system, "Segoe UI", "Open Sans", "Helvetica Neue", sans-serif;
  background-color: #212121;
  color: #f6f6f6;
  font-size: 18px;
  text-align: center;
}

.container {
  display: flex;
  flex-direction: column;
}

.row1 {
  background: #212121;
}

.header-container {
  padding: 30px;
  max-width: 800px;
  height: 550px;
  margin: 30px auto;
  background: #212121;
  background: linear-gradient(to left, transparent, #00000060, #212121), url("../img/bob-marley-1.jpg") no-repeat center;
  background-position: right;
  background-size: 486px;
  background-repeat: no-repeat;
}

#title {
  color: #6d9886;
  font-size: 8em;
  text-transform: uppercase;
  text-align: left;
  font-weight: bold;
  text-shadow: 3px 3px 3px black;
}

@media (max-width: 600px) {
  .header-container {
    padding: 10px;
  }

  #title {
    font-size: 3.5em;
    margin-top: 400px;
  }

  #line {
    width: 250px;
  }
}
.header-container h3 {
  color: #f6f6f6;
  text-align: left;
  font-weight: bold;
  text-shadow: 1px 1px 2px black;
}

.row2 {
  color: #212121;
  background: #f6f6f6;
  padding: 0px 30px;
}

.row3 {
  color: #212121;
  background: #6d9886;
}

.row4 {
  color: #212121;
  background: #f6f6f6;
  padding: 30px;
}

#headline {
  margin: 35px 0;
  text-align: center;
  font-weight: bold;
}

ul {
  max-width: 766px;
  margin: 0 auto 50px auto;
  text-align: left;
  line-height: 1.6;
}

li {
  list-style: disc;
  margin: 16px 0;
}

img {
  max-width: 100%;
  display: block;
  height: auto;
  background: #f6f6f6;
}

figcaption {
  margin: 10px 0 5px 0;
  font-style: italic;
}

#img-div {
  max-width: 766px;
  padding: 10px;
  margin: 30px auto 15px auto;
}

blockquote {
  padding: 10px;
  font-style: italic;
  font-family: "Lobster", cursive;
  font-size: 30px;
}

a {
  color: #f6f6f6;
}

a:visited {
  color: #212121;
}

.row3 h3 {
  color: #212121;
  padding-bottom: 30px;
}

a:hover {
  color: #6d9886;
}

footer {
  padding: 20px;
  font-size: 14px;
}