/* Define standard variables and values for website */
$color1: #212121;
$color2: #f6f6f6;
$color3: #6d9886;
$color4: #d9cab3;
$fontsize: 18px;
$fontsize2: 20px;
$titlefont: 130px;
$blockquoutefont: 30px;
$footerfont: 14px;

/* Use the variables */
body {
  font-family: "Roboto", -apple-system, "Segoe UI", "Open Sans",
    "Helvetica Neue", sans-serif;
  background-color: $color1;
  color: $color2;
  font-size: $fontsize;
  text-align: center;
}
.container {
  display: flex;
  flex-direction: column;
}
.row1 {
  background: $color1;
}
.header-container {
  padding: 30px;
  max-width: 800px;
  height: 550px;
  margin: 30px auto;
  background: $color1;
  background: linear-gradient(to left, transparent, #00000060, #212121),
    url("../img/bob-marley-1.jpg") no-repeat center;
  background-position: right;
  background-size: 486px;
  background-repeat: no-repeat;
}
#title {
  color: $color3;
  font-size: 8em;
  text-transform: uppercase;
  text-align: left;
  font-weight: bold;
  text-shadow: 3px 3px 3px black;
}
@media (max-width: 600px) {
  .header-container {
    padding: 10px;
  }
  #title {
    font-size: 3.5em;
    margin-top: 400px;
  }
  #line {
    width: 250px;
  }
}

.header-container h3 {
  color: $color2;
  text-align: left;
  font-weight: bold;
  text-shadow: 1px 1px 2px black;
}

.row2 {
  color: $color1;
  background: $color2;
  padding: 0px 30px;
}
.row3 {
  color: $color1;
  background: $color3;
}
.row4 {
  color: $color1;
  background: $color2;
  padding: 30px;
}
#headline {
  margin: 35px 0;
  text-align: center;
  font-weight: bold;
}
ul {
  max-width: 766px;
  margin: 0 auto 50px auto;
  text-align: left;
  line-height: 1.6;
}
li {
  list-style: disc;
  margin: 16px 0;
}
img {
  max-width: 100%;
  display: block;
  height: auto;
  background: $color2;
}
figcaption {
  margin: 10px 0 5px 0;
  font-style: italic;
}
#img-div {
  max-width: 766px;
  padding: 10px;
  margin: 30px auto 15px auto;
}
blockquote {
  padding: 10px;
  font-style: italic;
  font-family: "Lobster", cursive;
  font-size: $blockquoutefont;
}
a {
  color: $color2;
}
a:visited {
  color: $color1;
}
.row3 h3 {
  color: $color1;
  padding-bottom: 30px;
}
a:hover {
  color: $color3;
}
footer {
  padding: 20px;
  font-size: $footerfont;
}
